  position: relative;
  display: block;
  padding-top: 7px;
  padding-left: 45px;
  cursor: pointer;
  user-select: none; }

.support__agree input + label::before {
  content: "";
  position: absolute;
  left: 12px;
  top: 9px;
  background-image: url("../../img/checked-off.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  user-select: none; }

.support__agree input:checked + label::after {
  content: "";
  position: absolute;
  left: 12px;
  top: 9px;
  background-image: url("../../img/checked-on.svg");
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  user-select: none; }

